<style scoped>
    .type {
        margin-bottom: 10px;
    }
</style>
<template>
   <div>
       <RadioGroup class="type" v-model="type" type="button">
            <Radio label="all"></Radio>
            <Radio label="4">IP</Radio>
            <Radio label="5">CPU</Radio>
            <Radio label="6"></Radio>
            <Radio label="7"></Radio>
            <Radio label="8"></Radio>
            <Radio label="9"></Radio>
        </RadioGroup>
        <Table :columns="columns" :data="data"></Table>
        <Page class="page" @on-change="toPage" :total="allData.length" />
   </div>
</template>
<script>
    import axios from "axios";
    export default {
         data () {
            return {
                type: "all",
                columns: [
                    {
                        title: '',
                        key: 'order_sn'
                    },
                    {
                        title: '',
                        key: 'business_name'
                    },
                    {
                        title: '',
                        key: 'duration'
                    },
                    {
                        title: '',
                        key: 'machine_sn'
                    },
                    {
                        title: '',
                        key: 'resource_type'
                    },
                    {
                        title: '',
                        key: 'order_status'
                    },
                    {
                        title: '',
                        key: 'order_type'
                    },
                    {
                        title: '',
                        key: 'pay_type'
                    },
                    {
                        title: '',
                        key: 'payable_money'
                    },
                    {
                        title: '',
                        key: 'action',
                        width: 170,
                        align: 'center',
                        render: (h, params) => {
                            let operat = [
                                h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            this.show(params.row);
                                        }
                                    }
                                }, '')
                            ];
                            if(params.row.order_status=="") {
                                operat.push(h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            let isOk = confirm(`${params.row.payable_money}`);
                                            if(isOk) {
                                                axios.get("http://"+location.hostname+"/home/customer/payOrderByBalance",{
                                                    params: {
                                                        order_id: params.row.id
                                                    }
                                                }).then(res => {
                                                    if(res.data.code==1) {
                                                        alert("");
                                                         this.getData();
                                                    } else {
                                                        alert(res.data.msg);
                                                    }
                                                });
                                            }
                                        }
                                    }
                                }, ''));
                                operat.push(h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        // marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            let isOk = confirm("");
                                            if(isOk) {
                                                axios.get("http://"+location.hostname+"/home/customer/delOrder",{
                                                    params: {
                                                         order_id: params.row.id
                                                    }
                                                }).then(res => {
                                                    if(res.data.code==1) {
                                                        alert("");
                                                         this.getData();
                                                    } else {
                                                        alert(res.data.msg);
                                                    }
                                                });
                                            }
                                        }
                                    }
                                }, ''));
                            }
                            return h('div',operat);
                        }
                    }
                ],
                data: [
                ],
                allData: [],
                page: 0,
                pageTotal: 10,
            }
        },
        methods: {
            toPage(page) {
                // console.log(page);
                let start = (page - 1) * this.pageTotal;
                let end = (page - 1) * this.pageTotal + this.pageTotal;
                this.data = this.allData.sort((a,b) => b.id - a.id).slice(start,end);
            },
             show (data) {
                this.$Modal.info({
                    title: '',
                    content: `
                        : ${data.order_sn}<br />
                        ${data.after_money}<br />
                        ${data.before_money}<br />
                        ${data.business_sn}<br />
                        : ${data.business_name}<br />
                        : ${data.duration}<br />
                        : ${data.machine_sn}<br />
                        : ${data.order_note}<br />
                        : ${data.order_status}<br />
                        : ${data.order_type}<br />
                        : ${data.pay_price}<br />
                        : ${data.pay_time}<br />
                        : ${data.pay_type}<br />
                        : ${data.payable_money}<br />
                        : ${data.price}<br />
                        : ${data.resource}<br />
                        : ${data.resource_type}<br />
                        : ${data.serial_number}<br />
                        `
                })
            },
            getData() {
                 axios.get("http://"+location.hostname+"/home/customer/orderList").then(res => {
                    if(res.data.code==1) {
                        this.allData = res.data.data;
                        this.data = this.allData.sort((a,b) => b.id - a.id).slice(this.page,this.pageTotal);
                    }else if(res.data.code==5000) {
                        location.href = "http://"+location.hostname+"/tz/login.html";
                    } else {
                        console.error(res.data.msg);
                    }
                });
            }
        },
        mounted() {
           this.getData();
        }
    }
</script>